export const CHAT_LOADING_TYPE = "CHAT_LOADING";
export const CHAT_SUCCESS_TYPE = "CHAT_SUCCESS";
export const CHAT_ERROR_TYPE = "CHAT_ERROR";
export const FORM_LOADING_TYPE = "FORM_LOADING";
export const FORM_SUCCESS_TYPE = "FORM_SUCCESS";
export const FORM_ERROR_TYPE = "FORM_ERROR";
export function getChatLoadingAction() {
    return {
        type: CHAT_LOADING_TYPE,
        payload: {
            isLoading: true,
        },
    };
}
export function getChatSuccessAction(data) {
    return {
        type: CHAT_SUCCESS_TYPE,
        payload: {
            isLoading: false,
            data,
        },
    };
}
export function getChatErrorAction(error) {
    return {
        type: CHAT_ERROR_TYPE,
        payload: {
            isLoading: false,
            error,
        },
    };
}
export function getFormLoadingAction() {
    return {
        type: FORM_LOADING_TYPE,
        payload: {
            isLoading: true,
        },
    };
}
export function getFormSuccessAction() {
    return {
        type: FORM_SUCCESS_TYPE,
        payload: {
            isLoading: false,
        },
    };
}
export function getFormErrorAction(error) {
    return {
        type: FORM_ERROR_TYPE,
        payload: {
            isLoading: false,
            error,
        },
    };
}
