import { getMessagesList, sendMessage } from "./messagesApi";
import { store } from "./store";
import * as Actions from "./action";
import { parseSmiles } from "./smiles";
export async function getChatMessages() {
    store.dispatch(Actions.getChatLoadingAction());
    try {
        // @ts-ignore
        const allMessages = await getMessagesList();
        let messagesHtml = "";
        allMessages.map((currentValue) => {
            const realName = currentValue.nickname
                ? currentValue.nickname
                : currentValue.name;
            const message = parseSmiles(currentValue.message);
            messagesHtml = `${messagesHtml}<div><b>${realName}</b>: ${message}</div>`;
            return messagesHtml;
        });
        store.dispatch(Actions.getChatSuccessAction(messagesHtml));
    }
    catch (error) {
        store.dispatch(Actions.getChatErrorAction(`Ошибка получения сообщений. ${error.message}`));
    }
}
export async function sendChatMessage(nickname, message) {
    store.dispatch(Actions.getFormLoadingAction());
    try {
        const date = Date.now();
        const currentMessage = {
            message,
            nickname,
            now: date,
        };
        // @ts-ignore
        const response = await sendMessage(currentMessage);
        if (response.name) {
            await getChatMessages();
            store.dispatch(Actions.getFormSuccessAction());
        }
        else {
            store.dispatch(Actions.getFormErrorAction("Ошибка отправки сообщения"));
        }
    }
    catch (error) {
        store.dispatch(Actions.getFormErrorAction(`Ошибка отправки сообщения. ${error.message}`));
    }
}
