import { initialState } from "./state";
import * as Actions from "./action";
export const chatReducer = function chatReducer(state = initialState, action) {
    switch (action.type) {
        case Actions.CHAT_LOADING_TYPE:
            return Object.assign(Object.assign({}, state), { isLoading: action.payload.isLoading });
        case Actions.CHAT_SUCCESS_TYPE:
            return Object.assign(Object.assign({}, state), { isLoading: action.payload.isLoading, data: action.payload.data, error: "" });
        case Actions.CHAT_ERROR_TYPE:
            return Object.assign(Object.assign({}, state), { isLoading: action.payload.isLoading, data: "", error: action.payload.error });
        default:
            return state;
    }
};
